/**
 * Styles in this file are loaded separately by ckeditor wysiwyg fields.
 */
@import 'tokens/mixins';

.cke_editable {
  background: #fff;
  color: #323a45;
  font-family: 'Source Sans Pro', Sans-Serif;
  font-size: 15px;
  margin: 0.5em; /* to provide padding within ckeditor */
  max-width: 700px;

  @include va-basic-styling;
}
